var loadVoteFlag = false; //为加载投票加锁
	
export default {
	data() {
		return {
			startPageX: 0,
			currentView: DEFAULT_PAGE,
			data: {
				toView: `card_${DEFAULT_PAGE}`,
				list: ['Javascript', 'Typescript', 'Java', 'PHP', 'Go']
			},
			showList: [],
			userInfo: '',
			totalPage: 1,
			currentPage: 1
		};
	},

	onLoad(){
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)){
			uni.redirectTo({
				url: '../signin/signin'
			})
			return;
		} else {
			this.userInfo = userInfo;
		}
		this.getScreenSize(); //获取手机型号
		
		this.showVotes(this.currentPage);
	},

	onShow() {
		this.setTabBarIndex(1); //index为当前tab的索引
		
	},

	methods: {
		singleImgeFit(e) {
			var height = e.detail.height;
			var width = e.detail.width;
			var rate;
			if (height >= width) {
				this.singleImgState = 0;
				this.singleImgHeight = 400;
				rate = width / height;
				this.heightWidthRate = rate;
				this.singleImgWidth = 400 * rate;
				// console.log(this.singleImgState);
				// console.log(rate);
				// console.log(this.singleImgHeight);
				// console.log(this.singleImgWidth);
			} else {
				this.singleImgState = 1;
				this.singleImgWidth = 400;
				rate = height / width;
				this.heightWidthRate = rate;
				this.singleImgHeight = 400 * rate;
				// console.log(this.singleImgState);
				// console.log(rate);
				// console.log(this.singleImgHeight);
				// console.log(this.singleImgWidth);
			}
			// console.log(e.detail);
		},
		
		showVotes: function(page) {
			if (loadVoteFlag) {
				return;
			}
			loadVoteFlag = true;
			
			uni.showLoading({
				title: '加载中...'
			});
			setTimeout(() => {
				if(loadVoteFlag){
					loadVoteFlag = false; //解锁
					uni.hideLoading();
					uni.showToast({
						title: '网络未知错误',
						icon: 'none',
						duration: 1000
					});
				}
			}, 5000) // 延时5s timeout
			
			var that = this;
			console.log(1);
			uni.request({
				url: that.$serverUrl + '/vote/queryAllVotes',
				method: 'POST',
				data:{
					page: page,
					userId: that.userInfo.id
				},
				header:{
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					uni.hideLoading();
					// loadArticleFlag = false;
					console.log(res);
				}
			})
			
		}
		
	}
};
